@import 'variables';
@import 'mixins';
@import 'layout';
@import 'form';
@import 'date-picker';


/* --- text handling --- */
.center-text {
	text-align: center;
}

.spin {
  animation: spin 2s infinite linear;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}